<template>
    <div class="container mt-5">
      <b-card class="mb-3">
        <h2 class="mb-4">대화형 기도문 작성 도우미</h2>
  
        <!-- Step 1: 기도문 주제 입력 -->
        <b-form @submit.prevent="submitPrayerRequest" v-if="conversationStep === 1">
          <b-form-group
            label="기도문 주제:"
            label-for="prayerTopicInput"
            description="기도문 주제를 입력하세요.">
            <b-form-input
              id="prayerTopicInput"
              v-model="prayerTopic"
              required
              placeholder="예: 평안, 감사, 인도하심 등"
            ></b-form-input>
          </b-form-group>
          <b-button type="submit" variant="primary">기도문 생성</b-button>
        </b-form>
  
        <!-- Step 2: AI가 응답한 초안 -->
        <div v-if="conversationStep === 1">
          <b-card class="mt-4">
            <h3>AI가 작성한 기도문 초안:</h3>
            <p>{{ generatedPrayer }}</p>
          </b-card>
          <b-form-group label="더 추가하고 싶은 내용이 있나요?">
            <b-form-input
              v-model="userAdditionalInput"
              placeholder="추가적인 내용이나 수정사항을 입력하세요."
            ></b-form-input>
          </b-form-group>
          <b-button variant="success" @click="submitAdditionalInput">추가</b-button>
          <b-button variant="primary" @click="finalizePrayer">완성</b-button>
        </div>
  
        <!-- Step 3: 최종 기도문 -->
        <div v-if="conversationStep === 1">
          <b-card class="mt-4">
            <h3>최종 기도문:</h3>
            <p>{{ generatedPrayer }}</p>
          </b-card>
          <b-button variant="success" @click="restart">다시 시작하기</b-button>
        </div>
  
        <!-- 로딩 스피너 -->
        <b-spinner v-if="isLoading" label="기도문 생성 중..." type="grow"></b-spinner>
  
        <!-- 오류 메시지 -->
        <b-alert v-if="error" variant="danger" class="mt-4">
          기도문 생성에 실패했습니다. 다시 시도해주세요.
        </b-alert>
      </b-card>
    </div>
  </template>
  
  <script>
  import axios from 'axios';
  
  export default {
    data() {
      return {
        prayerTopic: '',              // 사용자 입력 값
        generatedPrayer: '',          // AI가 생성한 기도문
        userAdditionalInput: '',      // 추가적으로 사용자 수정 요청
        conversationStep: 1,          // 대화 단계
        isLoading: false,             // 로딩 상태
        error: null                   // 에러 메시지
      };
    },
    methods: {
      async submitPrayerRequest() {
        this.isLoading = true;
        this.error = null;
  
        try {
          const response = await axios.post('/api/prayer/generate', {
            topic: this.prayerTopic
          });
          this.generatedPrayer = response.data;
          this.conversationStep = 2; // AI 응답 후 다음 단계로 이동
        } catch (err) {
          this.error = 'API 요청 중 오류가 발생했습니다.';
        } finally {
          this.isLoading = false;
        }
      },
      async submitAdditionalInput() {
        if (this.userAdditionalInput) {
          this.isLoading = true;
          this.error = null;
  
          try {
            const response = await axios.post('/api/prayer/generate', {
              topic: this.generatedPrayer + ' ' + this.userAdditionalInput
            });
            this.generatedPrayer = response.data; // 업데이트된 기도문
            this.userAdditionalInput = ''; // 입력 초기화
          } catch (err) {
            this.error = 'API 요청 중 오류가 발생했습니다.';
          } finally {
            this.isLoading = false;
          }
        }
      },
      finalizePrayer() {
        this.conversationStep = 3; // 최종 기도문 단계로 이동
      },
      restart() {
        this.conversationStep = 1;
        this.generatedPrayer = '';
        this.prayerTopic = '';
        this.userAdditionalInput = '';
      }
    }
  };
  </script>
  
  <style scoped>
  .container {
    max-width: 600px;
  }
  </style>
  